import Vue from 'vue'
import Router from 'vue-router'
import FormProducer from '@/components/FormProducer/FormProducer.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'FormProducer',
      component: FormProducer
    }
  ]
})



