<template>
  <div>
    <div class="spiltArea"></div>

    <div class="form_container">
      <div class="form_row form_row_head">
        <div class="form_nav_col" v-for="item in tableHead" :key="item.mainTitle">
          <p class="text" v-if="!hasCategory(item)">
            {{ item.mainTitle }}
          </p>

          <div v-else>
            <div class="form_nav_col_child_1">
              <p class="text">
                {{ item.mainTitle }}
              </p>
            </div>
            <div class="form_nav_col_child_1">
              <div class="form_nav_col_child_2" v-for="item1 in item.category" :key="item1.mainTitle">
                <p class="text" v-if="!hasCategory(item1)">
                  {{ item1.mainTitle }}
                </p>
                <div class="form_nav_col_child_2" v-else>
                  <div class="form_nav_col_child_3">
                    <p class="text">
                      {{ item1.mainTitle }}
                    </p>
                  </div>
                  <div class="form_nav_col_child_3">
                    <div class="form_nav_col_child_4" v-for="item2 in item1.category" :key="item2.mainTitle">
                      <div>
                        <p class="text" v-if="!hasCategory(item2)">
                          {{ item2.mainTitle }}
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="form_row" v-for="(i,index1) in tmpTableContent" :key="index1">
        <p></p>
        <p class="text" v-for="j in colNums" :class="'form_content_' + index1 + '_' + j" :key="j">
          <input type="text" v-model="i[j-1]">
        </p>
      </div>
    </div>

    <div class="spiltArea"></div>

    <div>
      {{ tmpTableContent }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'FormProducer',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      len: 0,
      tableHead: [
        { mainTitle: '征收项目', category: [] },
        { mainTitle: '征收品目', category: [] },
        {
          mainTitle: '计税（费）依据',
          category: [
            {
              mainTitle: '增值税',
              category: [
                {
                  mainTitle: '一般增值税',
                  category: []
                },
                {
                  mainTitle: '免抵税额',
                  category: []
                }
              ]
            },
            {
              mainTitle: '消费税',
              category: []
            },
            {
              mainTitle: '营业税',
              category: []
            },
            {
              mainTitle: '合计',
              category: []
            }
          ]
        }
      ],
      tableContent: [
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', ''],
        ['', '', '', '', '', '', '']
        // [ '城市维护建设税', '市区（增值税附征）', 23, 22, 11, 55, '' ],
        // [ '城市维护建设税', '市区（增值税附征）', 23, 22, 11, 55, '' ]
      ]
    }
  },
  computed: {
    colNums: function () {
      let arr = this.tableHead
      let that = this
      that.len = 0
      return this.getArrLen(arr)
    },
    tmpTableContent: function () {
      return this.formatTableContent(this.tableContent)
    }
  },
  mounted () {
  },
  methods: {
    hasCategory: item => {
      return !!item && !!item.category && item.category.length > 0
    },
    getArrLen (arr) {
      arr.forEach(ele => {
        // console.log(ele)
        if (!!ele.category && ele.category.length < 1) {
          this.len++
          // console.log('len', this.len)
        } else {
          this.getArrLen(ele.category)
        }
      })
      return this.len
    },
    formatTableContent (arr) {
      arr.map(item => {
        if (!!item && item.length > 0) {
          let tmpAll = 0
          for (let i = 2; i < item.length - 1; i++) {
            if (item[i]) {
              tmpAll += parseInt(item[i], 10)
            }
          }
          item[item.length - 1] = tmpAll
        }
        // console.log('curItem:' + JSON.stringify(item))
      })
      // console.log('afterArr:' + JSON.stringify(arr))
      return arr
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
@import './formProducer.less';
</style>



